<template>
  <main class='chat'>
    <div class='srch_bar'>
      <i class='fa fa-search'></i>
      <input type='text' placeholder='Lets search the friends'>
    </div>
    <ul class='chat_list'>
      <chat-list v-for='(li,index) in chatlist' :li='li' :key='index'></chat-list>
    </ul>
    <div class='empty'></div>
  </main>
</template>
<script>
import chatlist from '../data/chatlist.js'
import ChatList from './ChatList.vue'
export default {
  name: 'chats',
  data () {
    return {
      title: 'chats',
      chatlist
    }
  },
  components: {
    'chat-list': ChatList
  },
  mounted () {
    this.$parent.$emit(
      'changeData',
      'Chats',
      'fas fa-search',
      'fas fa-user-plus',
      'fas fa-music',
      'fas fa-cog'
    )
  }
}
</script>
<style lang='scss' src='../styles/chats.scss'>
</style>
