  data () {
    return {
      menu: '',
      icon1: '',
      icon2: '',
      icon3: '',
      icon4: ''
    }
  },
  destroyed () {
    this.$off('changeData')
  },
  created () {
    const _self = this
    this.$on('changeData', function (
      newMenu,
      newIcon1,
      newIcon2,
      newIcon3,
      newIcon4
    ) {
      _self.menu = newMenu
      _self.icon1 = newIcon1
      _self.icon2 = newIcon2
